import React, { useState, useCallback, useEffect } from "react";
import {
  GoogleMap,
  useJsApiLoader,
  DirectionsRenderer,
  Marker,
} from "@react-google-maps/api";
import { MARKERS_PATHS } from "@assets/svg";
import styled from "styled-components";
import { useLocation } from "react-router-dom";
import { ROUTES } from "@constants/routes";
import { useRecoilValue } from "recoil";
import {
  currentCoordState,
  selectedAttractionCoordState,
  selectedFestivalCoordState,
  selectedRestaurantCoordState,
  selectedShoppingCoordState,
} from "@states/aroundAtoms";
import { selectedResidencyCoordState } from "@states/sattlementAtoms";
import { courseDataState, routeDataState } from "@states/travelAtoms";
import { accordionOpenState } from "@states/accordionAtoms";

function mappingMarker(type: string) {
  if (type === "tourist_attraction")
    return MARKERS_PATHS.TOURIST_ATTRACTION_MARKER;
  else if (type === "shopping") return MARKERS_PATHS.SHOPPING_MARKER;
  else if (type === "experience") return MARKERS_PATHS.EXPERIENCE_MAEKER;
  else if (type === "accommodation") return MARKERS_PATHS.ACCOMMODATION_MARKER;
  else if (type === "restaurant") return MARKERS_PATHS.RESTAURANT_MARKER;
  else if (type === "location") return MARKERS_PATHS.LOCATION_MARKER;
  else if (type === "S1") return MARKERS_PATHS.S1_MARKER;
  else if (type === "S2") return MARKERS_PATHS.S2_MARKER;
  else if (type === "S3") return MARKERS_PATHS.S3_MARKER;
  else if (type === "company") return MARKERS_PATHS.COMPANY_MARKER;
  else if (type === "shopping") return MARKERS_PATHS.SHOPPING_MARKER;
  else if (type === "festival") return MARKERS_PATHS.STAR_MARKER;
  else return MARKERS_PATHS.STAR_MARKER;
}

function MyMapComponent() {
  const location = useLocation();
  const accordionOpen = useRecoilValue(accordionOpenState);
  const GOOGLE_MAPS_API_KEY = "" + process.env.REACT_APP_GOOGLE_MAPS_API_KEY;
  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: GOOGLE_MAPS_API_KEY, // 여기에 API 키 입력
    libraries: ["places"],
  });
  const selectedRestaurantCoord = useRecoilValue(selectedRestaurantCoordState);
  const selectedFestivalCoord = useRecoilValue(selectedFestivalCoordState);
  const selectedAttractionCoord = useRecoilValue(selectedAttractionCoordState);
  const selectedShoppingCoord = useRecoilValue(selectedShoppingCoordState);
  const selectedResidencyCoord = useRecoilValue(selectedResidencyCoordState);
  const currentCoord = useRecoilValue(currentCoordState);
  const courseData = useRecoilValue(courseDataState);
  const routeData = useRecoilValue(routeDataState);
  const [center, setCenter] = useState({ lat: 35.17013, lng: 129.12848 });

  useEffect(() => {
    selectedResidencyCoord &&
      setCenter({
        lat: selectedResidencyCoord.lat,
        lng: selectedResidencyCoord.lng,
      });
  }, [selectedResidencyCoord]);

  const [directions, setDirections] =
    useState<google.maps.DirectionsResult | null>(null);

  const calculateRoute = useCallback((origin: any, destination: any) => {
    const directionsService = new google.maps.DirectionsService();

    directionsService.route(
      {
        origin,
        destination,
        travelMode: google.maps.TravelMode.TRANSIT, // 운전 경로를 선택 (WALKING, BICYCLING, TRANSIT 가능)
      },
      (result, status) => {
        if (status === "OK" && result) {
          setDirections(result);
        } else {
          console.error(`error fetching directions ${result}`);
        }
      }
    );
  }, []);

  useEffect(() => {
    if (location.pathname === ROUTES.TEAVEL && routeData)
      calculateRoute(routeData.start, routeData.end);
  }, [routeData]);

  const containerStyle = {
    width: accordionOpen ? "75vw" : "100vw",
    height: "100vh",
  };

  return isLoaded ? (
    <div>
      <GoogleMap mapContainerStyle={containerStyle} center={center} zoom={15}>
        {location.pathname.includes(ROUTES.STAY) && selectedResidencyCoord && (
          <Marker
            position={{
              lat: selectedResidencyCoord.lat,
              lng: selectedResidencyCoord.lng,
            }}
            icon={mappingMarker(selectedResidencyCoord.marker)}
          />
        )}
        {/* 내 주변 탭에서 현재 내 좌표에 마커 */}
        {location.pathname.includes(ROUTES.AROUND) && currentCoord && (
          <Marker
            position={{
              lat: currentCoord.lat,
              lng: currentCoord.lng,
            }}
            icon={mappingMarker("location")}
          />
        )}
        {/* 내 주변 탭의 식당 리스트에서 특정 식당을 선택한 경우 해당 식당 좌표에 마커 */}
        {location.pathname.includes(ROUTES.AROUND_RESTAURANT) &&
          selectedRestaurantCoord && (
            <Marker
              position={{
                lat: selectedRestaurantCoord.lat,
                lng: selectedRestaurantCoord.lng,
              }}
              icon={mappingMarker("restaurant")}
            />
          )}

        {/* 내 주변 탭의 축제/행사 리스트에서 특정 축제/행사을 선택한 경우 해당 축제/행사 좌표에 마커 */}
        {location.pathname.includes(ROUTES.AROUND_FESTIVAL) &&
          selectedFestivalCoord && (
            <Marker
              position={{
                lat: selectedFestivalCoord.lat,
                lng: selectedFestivalCoord.lng,
              }}
              icon={mappingMarker("festival")}
            />
          )}

        {/* 내 주변 탭의 명소 리스트에서 특정 명소를 선택한 경우 해당 명소 좌표에 마커 */}
        {location.pathname.includes(ROUTES.AROUND_ATTRACTION) &&
          selectedAttractionCoord && (
            <Marker
              position={{
                lat: selectedAttractionCoord.lat,
                lng: selectedAttractionCoord.lng,
              }}
              icon={mappingMarker("tourist_attraction")}
            />
          )}

        {/* 내 주변 탭의 쇼핑 리스트에서 특정 쇼핑을 선택한 경우 해당 쇼핑 좌표에 마커 */}
        {location.pathname.includes(ROUTES.AROUND_SHOPPING) &&
          selectedShoppingCoord && (
            <Marker
              position={{
                lat: selectedShoppingCoord.lat,
                lng: selectedShoppingCoord.lng,
              }}
              icon={mappingMarker("shopping")}
            />
          )}
        {location.pathname === ROUTES.TEAVEL && (
          <>
            {directions && (
              <>
                <DirectionsRenderer
                  directions={directions}
                  options={{ suppressMarkers: true }}
                />
              </>
            )}
            {/* 카테고리에 따라 마커 표시 */}
            {courseData &&
              courseData.course.map((location, i) => (
                <Marker
                  key={i}
                  position={{ lat: location.latitude, lng: location.longitude }}
                  icon={mappingMarker(location.type)} // 카테고리에 맞는 아이콘 설정
                />
              ))}
          </>
        )}
      </GoogleMap>
    </div>
  ) : (
    <div>지도 로딩 중...</div>
  );
}

export default React.memo(MyMapComponent);
