import {
  Residency,
  SearchResidencyCriteria,
} from "@services/settlementService";
import { atom } from "recoil";

export const residencyDataState = atom<Residency[] | null>({
  key: "residencyDataState",
  default: null,
});

export const selectedResidencyDataState = atom<Residency | null>({
  key: "selectedResidencyDataState",
  default: null,
});

export const selectedResidencyCoordState = atom<{
  lat: number;
  lng: number;
  marker: string;
} | null>({
  key: "selectedResidencyCoordState",
  default: null,
});

export const selectedResidencyCriteriaState =
  atom<SearchResidencyCriteria | null>({
    key: "selectedResidencyCriteriaState",
    default: null,
  });
