import {
  AttractionData,
  FestivalData,
  RestaurantData,
  ShoppingData,
} from "../types/around";
import axios from "axios";
import config from "../config";

const API_BASE_URL = config.apiBaseUrl;

export const fetchRestaurant = async (origin: {
  latitude: number;
  longitude: number;
}): Promise<RestaurantData[]> => {
  const url = `${API_BASE_URL}/api/v1/map/restaurants/nearby`;
  const params = {
    latitude: `${origin.latitude}`,
    longitude: `${origin.longitude}`,
  };

  try {
    const response = await axios.get(url, { params });
    return response.data.data;
  } catch (error) {
    console.error("Error fetching restaurant data:", error);
    throw error;
  }
};

export const fetchRestaurantDetail = async (id: number) => {
  const url = `${API_BASE_URL}/api/v1/map/restaurants/${id}`;

  try {
    const response = await axios.get(url);
    return response.data.data;
  } catch (error) {
    console.error("Error fetching restaurant detail data:", error);
    throw error;
  }
};

