import axios from "axios";

// Types
export interface SearchResidencyCriteria {
  subwayStation: "near" | "mid" | "far";
  publicParking: "near" | "mid" | "far";
  traditionalMarket: "near" | "mid" | "far";
  policeStation: "near" | "mid" | "far";
  kindergarten: "near" | "mid" | "far";
  elementarySchool: "near" | "mid" | "far";
  middleSchool: "near" | "mid" | "far";
  highSchool: "near" | "mid" | "far";
  beach: "near" | "mid" | "far";
  park: "near" | "mid" | "far";

  studyRoom: boolean;
  tutorAcademy: boolean;
  artAcademy: boolean;
  foreignLanguageAcademy: boolean;
  musicAcademy: boolean;
  danceAcademy: boolean;
  slicedRawFish: boolean;
  koreanFood: boolean;
  japaneseFood: boolean;
  chineseFood: boolean;
  coffeeShop: boolean;
  convenienceStore: boolean;
  crimePreventionFacility: boolean;
  customLatitude: number;
  customLongitude: number;
}

interface NearestItem {
  item: string;
  dist: number;
  dist_group: number;
  id: number;
  name: string;
  latitude: number;
  longitude: number;
}

interface JabListItem {
  address: string;
  company: string;
  companyUrl: string;
  distance: number;
  educationLevel: string;
  experience_level: number | null;
  id: string;
  industry: string;
  jobType: string;
  location: number;
  latitude: number;
  longitude: string;
  pageUrl: string;
  salary: string;
  title: string;
}

export interface Residency {
  id: number;
  district: string;
  address: string;
  household: number;
  type: string;
  price: string;
  imgUrl: string;
  latitude: number;
  longitude: number;
  nearestItemList: NearestItem[];
  jobList: JabListItem[];
}

interface ApiResponse {
  success: boolean;
  data: Residency[];
}

// API function
export const fetchResidency = async (
  criteria: SearchResidencyCriteria
): Promise<Residency[]> => {
  try {
    const response = await axios.post<ApiResponse>(
      "/api/v1/residency/search",
      criteria
    );
    console.log("get fetchResidency: ", response.data.data);
    return response.data.data;
  } catch (error) {
    console.error("Error fetching houses:", error);
    throw error;
  }
};
