import axios from "axios";

// Types
export interface SearchTravelCriteria {
  eatingCount: number;
  eatingTypes: string[];
  likeMenus: string;
  eatingRequirements: string;
  shoppingCount: number;
  shoppingRequirements: string;
  touristAttractionCount: number;
  touristAttractionRequirements: string;
  experienceCount: number;
  experienceRequirements: string;
  accommodationCount: number;
  accommodationKeywords: string[];
  isUsingCar: boolean;
  maxMoveMin: number;
  sequence: string[];
}

export interface Course {
  type: string;
  name: string;
  description: string | null;
  latitude: number;
  longitude: number;
  usageDay: string | null;
  holiday: string | null;
  usageTime: string | null;
  imageURL: string | null;
  usagePrice: string | null;
  contact: string | null;
  repMenu: string | null;
}

export interface CourseData {
  course: Course[];
  moveMinutes: number[];
}

// API function
export const fetchCourse = async (
  criteria: SearchTravelCriteria
): Promise<CourseData> => {
  try {
    const response = await axios.post<CourseData>("/api/v1/tourism", criteria);
    console.log("get fetchCourse: ", response.data);
    return response.data;
  } catch (error) {
    console.error("Error fetching course:", error);
    throw error;
  }
};
