import React from "react";
import { useRecoilValue } from "recoil";
import styled from "styled-components";
import Icon from "@components/Icon";
import { ICONS } from "@constants/icons";
import { selectedTravelState } from "@states/travelAtoms";
import { selectedResidencyDataState } from "@states/sattlementAtoms";

const Detail: React.FC = () => {
  const selectedTravelData = useRecoilValue(selectedTravelState);
  const selectedSettlementData = useRecoilValue(selectedResidencyDataState);

  function mappingIcon(name: string) {
    switch (name) {
      case "fsh_fs":
        return ICONS.FSH_FS;
      case "kindergarten":
        return ICONS.KINDERGARTEN;
      case "crime_prevention_facility":
        return ICONS.CRIME_PREVENTION_FACILITY;
      case "ch_fs":
        return ICONS.CH_FS;
      case "elementary_school":
        return ICONS.ELEMENTARY_SCHOOL;
      case "cf_fs":
        return ICONS.CF_FS;
      case "jp_fs":
        return ICONS.JP_FS;
      case "high_school":
        return ICONS.HIGH_SCHOOL;
      case "middle_school":
        return ICONS.MIDDLE_SCHOOL;
      case "tutor_acdm":
        return ICONS.TUTOR_ACDM;
      case "cvn_fs":
        return ICONS.CVN_FS;
      case "police":
        return ICONS.POLICE;
      case "studyroom_acdm":
        return ICONS.STUDYROOM_ACDM;
      case "art_acdm":
        return ICONS.ART_ACDM;
      case "foreign_acdm":
        return ICONS.FOREIGN_ACDM;
      case "beach":
        return ICONS.BEACH;
      case "traditional_market":
        return ICONS.TRADITIONAL_MARKET;
      case "kr_fs":
        return ICONS.KR_FS;
      case "music_acdm":
        return ICONS.MUSIC_ACDM;
      case "dance_acdm":
        return ICONS.DANCE_ACDM;
      case "park":
        return ICONS.PARK;
      case "parking_lot":
        return ICONS.PARKING_LOT;
      case "subway_station":
        return ICONS.SUBWAY_STATION;
      default:
        return ICONS.FSH_FS;
    }
  }

  function mappingType(name: string) {
    switch (name) {
      case "fsh_fs":
        return "횟집";
      case "kindergarten":
        return "유치원";
      case "crime_prevention_facility":
        return "치안시설";
      case "ch_fs":
        return "중식";
      case "elementary_school":
        return "초등학교";
      case "cf_fs":
        return "카페";
      case "jp_fs":
        return "일식";
      case "high_school":
        return "고등학교";
      case "middle_school":
        return "중학교";
      case "tutor_acdm":
        return "보습학원";
      case "cvn_fs":
        return "편의점";
      case "police":
        return "경찰서";
      case "studyroom_acdm":
        return "독서실";
      case "art_acdm":
        return "미술학원";
      case "foreign_acdm":
        return "외국어학원";
      case "beach":
        return "바다";
      case "traditional_market":
        return "전통시장";
      case "kr_fs":
        return "한식";
      case "music_acdm":
        return "음악학원";
      case "dance_acdm":
        return "무용학원";
      case "park":
        return "공원";
      case "parking_lot":
        return "주차장";
      case "subway_station":
        return "지하철역";
    }
  }

  return (
    <Wrapper>
      <ImageWrapper>
        {selectedSettlementData && selectedSettlementData.imgUrl ? (
          <Image src={selectedSettlementData.imgUrl} />
        ) : (
          <NoImage>
            <Icon name={ICONS.NO_IMAGE} />
          </NoImage>
        )}
      </ImageWrapper>
      <TitleWrapper>
        <div>
          <Category>{selectedSettlementData?.type}</Category>
          <Title>{selectedSettlementData?.district}</Title>
        </div>
        <Price>{selectedSettlementData?.price}</Price>
      </TitleWrapper>
      <ContentWrapper>
        <BoxWrapper>
          <Type>주변 인프라 및 시설 정보</Type>

          <Box>
            {selectedSettlementData &&
              selectedSettlementData.nearestItemList && (
                <>
                  {selectedSettlementData.nearestItemList.map((item) => (
                    <NearestItemWrapper>
                      <Icon name={mappingIcon(item.item)} size={13} />
                      <NearestItemType>
                        {mappingType(item.item)}
                      </NearestItemType>
                      <NearestItemName>{item.name}</NearestItemName>
                      <NearestItemDist>
                        {Math.ceil(item.dist * 100) / 100} km
                      </NearestItemDist>
                    </NearestItemWrapper>
                  ))}
                </>
              )}
          </Box>
        </BoxWrapper>

        <BoxWrapper>
          <Type>주변 일자리 정보</Type>

          <Box>
            {selectedSettlementData && selectedSettlementData.jobList && (
              <>
                {selectedSettlementData.jobList.map((job) => (
                  <div>{job.company}</div>
                ))}
              </>
            )}
          </Box>
        </BoxWrapper>
        {selectedTravelData && selectedTravelData.description && (
          <BoxWrapper>
            <Type>설명</Type>
            <Box>{selectedTravelData.description}</Box>
          </BoxWrapper>
        )}
      </ContentWrapper>
    </Wrapper>
  );
};

export default Detail;

const Wrapper = styled.div`
  background-color: #fcfcfc;
  height: 98vh;
  box-shadow: ${(props) => props.theme.boxShadow};
  border-radius: 10px;
`;

const ImageWrapper = styled.div`
  padding-top: 13px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: ${(props) => props.theme.colors.background};

  border-radius: 10px 10px 0 0;
`;

const Image = styled.img`
  width: 360px;
  height: 230px;
  border-radius: 6px;
  box-shadow: ${(props) => props.theme.boxShadow};
`;

const NoImage = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
  color: ${(props) => props.theme.colors.neutral.mediumGrey};

  width: 360px;
  height: 230px;
  border-radius: 6px;
  box-shadow: ${(props) => props.theme.boxShadow};
  background-color: ${(props) => props.theme.colors.neutral.lightGrey};
`;

const TitleWrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-left: 12px;
  background-color: ${(props) => props.theme.colors.background};
  padding-bottom: 20px;
  border-bottom: 2px solid ${(props) => props.theme.colors.neutral.lightGrey};
`;
const Title = styled.div`
  font-size: 20px;
  color: ${(props) => props.theme.colors.primary};
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
`;

const Category = styled.div`
  padding-top: 10px;
  padding-bottom: 3px;
  font-size: 12px;
  color: ${(props) => props.theme.colors.semantic.success};
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
`;

const Price = styled.div`
  display: flex;
  margin-top: 8px;
  align-items: end;
  font-size: 18px;
  color: ${(props) => props.theme.colors.neutral.darkGrey};
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
  padding-right: 20px;
`;

const ContentWrapper = styled.div`
  overflow-y: scroll;
  padding-bottom: 20px;
  border-radius: 0 0 10px 10px;
  width: 100%;
  min-height: 100%;
`;

const BoxWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Box = styled.div`
  line-height: 16px;
  width: 350px;
  padding: 15px;
  background-color: ${(props) => props.theme.colors.background};
  box-shadow: ${(props) => props.theme.boxShadow};
  font-family: ${(props) => props.theme.fonts.pretendard.regular};
  font-size: 13px;
  overflow-y: scroll;
  max-height: 165px;
`;

const Type = styled.div`
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
  font-size: 13px;
  margin-left: 15px;
  color: #4e4e4e;
  display: flex;
  margin-top: 20px;
  margin-bottom: 5px;
  align-self: flex-start;
`;

const NearestItemWrapper = styled.div`
  display: flex;
  margin-bottom: 3px;
`;

const NearestItemType = styled.div`
  margin-left: 10px;
  width: 70px;
  color: ${(props) => props.theme.colors.neutral.mediumGrey};
  /* font-family: ${(props) => props.theme.fonts.pretendard.bold}; */
`;

const NearestItemName = styled.div`
  width: 160px;
  color: ${(props) => props.theme.colors.neutral.darkGrey};
`;

const NearestItemDist = styled.div`
  display: flex;
  width: 60px;
  justify-content: flex-end;
`;
