import React from "react";
import { useRecoilValue } from "recoil";
import styled from "styled-components";
import Icon from "@components/Icon";
import { ICONS } from "@constants/icons";
import { selectedTravelState } from "@states/travelAtoms";

const Detail: React.FC = () => {
  const selectedTravelData = useRecoilValue(selectedTravelState);

  return (
    <Wrapper>
      <ImageWrapper>
        {selectedTravelData && selectedTravelData.imageURL ? (
          <Image src={selectedTravelData.imageURL} />
        ) : (
          <NoImage>
            <Icon name={ICONS.NO_IMAGE} />
          </NoImage>
        )}
      </ImageWrapper>
      <TitleWrapper>
        <Category>{selectedTravelData?.type}</Category>
        <Title>{selectedTravelData?.name}</Title>
      </TitleWrapper>
      <ContentWrapper>
        <BoxWrapper>
          <Type>운영 정보</Type>
          <Box>
            {selectedTravelData?.usageTime && (
              <Info>
                <Icon
                  name={ICONS.TIME}
                  size={14}
                  color="white"
                  hoverColor="white"
                />

                <p> {selectedTravelData.usageTime}</p>
              </Info>
            )}
            {selectedTravelData?.usagePrice && (
              <Info>
                <Icon
                  name={ICONS.COST}
                  size={14}
                  color="white"
                  hoverColor="white"
                />

                <p> {selectedTravelData.usagePrice}</p>
              </Info>
            )}

            {selectedTravelData?.contact && (
              <Info>
                <Icon
                  name={ICONS.PHONE}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <p> {selectedTravelData.contact}</p>
              </Info>
            )}
          </Box>
        </BoxWrapper>
        {selectedTravelData && selectedTravelData.description && (
          <BoxWrapper>
            <Type>설명</Type>
            <Box>{selectedTravelData.description}</Box>
          </BoxWrapper>
        )}
      </ContentWrapper>
    </Wrapper>
  );
};

export default Detail;

const Wrapper = styled.div`
  background-color: #fcfcfc;
  height: 98vh;
  box-shadow: ${(props) => props.theme.boxShadow};
  border-radius: 10px;
`;

const ImageWrapper = styled.div`
  padding-top: 13px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: ${(props) => props.theme.colors.background};

  border-radius: 10px 10px 0 0;
`;

const Image = styled.img`
  width: 360px;
  height: 230px;
  border-radius: 6px;
  box-shadow: ${(props) => props.theme.boxShadow};
`;

const NoImage = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
  color: ${(props) => props.theme.colors.neutral.mediumGrey};

  width: 360px;
  height: 230px;
  border-radius: 6px;
  box-shadow: ${(props) => props.theme.boxShadow};
  background-color: ${(props) => props.theme.colors.neutral.lightGrey};
`;

const TitleWrapper = styled.div`
  padding-left: 12px;
  background-color: ${(props) => props.theme.colors.background};
  padding-bottom: 20px;
  border-bottom: 2px solid ${(props) => props.theme.colors.neutral.lightGrey};
`;
const Title = styled.div`
  width: 350px;
  font-size: 20px;
  color: ${(props) => props.theme.colors.primary};
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
`;
const Category = styled.div`
  padding-top: 10px;
  padding-bottom: 3px;
  font-size: 12px;
  color: ${(props) => props.theme.colors.semantic.success};
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
`;

const ContentWrapper = styled.div`
  overflow-y: scroll;
  padding-bottom: 20px;
  border-radius: 0 0 10px 10px;
  width: 100%;
  min-height: 100%;
`;

const BoxWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Box = styled.div`
  line-height: 16px;
  width: 350px;
  padding: 15px;
  background-color: ${(props) => props.theme.colors.background};
  box-shadow: ${(props) => props.theme.boxShadow};
  font-family: ${(props) => props.theme.fonts.pretendard.regular};
  font-size: 13px;
  overflow-y: scroll;
  max-height: 165px;
`;

const Type = styled.div`
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
  font-size: 13px;
  margin-left: 15px;
  color: #4e4e4e;
  display: flex;
  margin-top: 20px;
  margin-bottom: 5px;
  align-self: flex-start;
`;

const Info = styled.div`
  display: flex;
  p {
    margin-left: 4px;
    color: #4e4e4e;
  }
`;
