import React, { useEffect, useState } from "react";
import { useRecoilValue, useSetRecoilState } from "recoil";
import {
  selectedAroundAttractionState,
  selectedAttractionCoordState,
} from "@states/aroundAtoms";
import { fetchFestivalDetail } from "@services/aroundService";
import {
  AttractionDetailData,
  FestivalDetailData,
} from "../../../types/around";
import styled from "styled-components";
import Icon from "@components/Icon";
import { ICONS } from "@constants/icons";

const AttractionDetail: React.FC = () => {
  const selectedAtteaction = useRecoilValue(selectedAroundAttractionState);
  const setAttractionCoord = useSetRecoilState(selectedAttractionCoordState);

  const [attractionDetail, setAttractionDetail] =
    useState<AttractionDetailData | null>(null);

  useEffect(() => {
    if (selectedAtteaction && "attractionId" in selectedAtteaction) {
      fetchFestivalDetail(selectedAtteaction.attractionId)
        .then((data) => {
          setAttractionDetail(data);
        })
        .catch((error) => {
          console.error("Failed to fetch Attraction details:", error);
        });
    }
  }, [selectedAtteaction]);

  useEffect(() => {
    console.log("Attraction Detail State:", attractionDetail);
  }, [attractionDetail]);

  if (!attractionDetail) {
    return <div>Loading...</div>;
  }

  const coord = {
    lat: attractionDetail.latitude,
    lng: attractionDetail.longitude,
  };
  setAttractionCoord(coord);

  return (
    <Wrapper>
      <ImageWrapper>
        <Image src={attractionDetail.thumbnailUrl} />
      </ImageWrapper>
      <TitleWrapper>
        <Category>축제/행사</Category>
        <Title>{attractionDetail.title}</Title>
      </TitleWrapper>
      <ContentWrapper>
        <BoxWrapper>
          <Type>운영 정보</Type>
          <Box>
            {attractionDetail.address && (
              <Info>
                <Icon
                  name={ICONS.ADDRESS}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <p> {attractionDetail.address}</p>
              </Info>
            )}

            {attractionDetail.usageTime && (
              <Info>
                <Icon
                  name={ICONS.TIME}
                  size={14}
                  color="white"
                  hoverColor="white"
                />

                <p> {attractionDetail.usageTime}</p>
              </Info>
            )}

            {attractionDetail.usagePrice && (
              <Info>
                <Icon
                  name={ICONS.COST}
                  size={14}
                  color="white"
                  hoverColor="white"
                />

                <p> {attractionDetail.usagePrice}</p>
              </Info>
            )}

            {attractionDetail.phone && (
              <Info>
                <Icon
                  name={ICONS.PHONE}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <p> {attractionDetail.phone}</p>
              </Info>
            )}

            {attractionDetail.homePageUrl && (
              <Info>
                <Icon
                  name={ICONS.WEBSITE}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <a href={attractionDetail.homePageUrl} target="__blank">
                  <p>{attractionDetail.homePageUrl}</p>
                </a>
              </Info>
            )}
          </Box>
        </BoxWrapper>
        <BoxWrapper>
          <Type>설명</Type>
          <Box>{attractionDetail.details}</Box>
        </BoxWrapper>
      </ContentWrapper>
    </Wrapper>
  );
};

export default AttractionDetail;

