import React from "react";
import styled from "styled-components";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { cardOpenState } from "@states/accordionAtoms";
import {
  courseDataState,
  routeDataState,
  selectedTravelState,
} from "@states/travelAtoms";
import Icon from "@components/Icon";
import { ICONS } from "@constants/icons";
import { Course } from "@services/travelService";

export const Result = () => {
  const setCardOpen = useSetRecoilState(cardOpenState);
  const courseData = useRecoilValue(courseDataState);
  const routeData = useSetRecoilState(routeDataState);
  const setSelectedTravelData = useSetRecoilState(selectedTravelState);

  const handleCardClick = (data: Course) => {
    setSelectedTravelData(data);
    setCardOpen(true);
  };

  const handleCarIconClick = (index: number) => {
    if (courseData && courseData.course.length > index + 1) {
      const currentItem = courseData.course[index];
      const nextItem = courseData.course[index + 1];
      const route = {
        start: { lat: currentItem.latitude, lng: currentItem.longitude },
        end: { lat: nextItem.latitude, lng: nextItem.longitude },
      };
      routeData(route);
    }
  };

  function mappingCategory(type: string) {
    switch (type) {
      case "restaurant":
        return "식당";
      case "tourist_attraction":
        return "명소";
      case "shopping":
        return "쇼핑";
      case "experience":
        return "체험";
      case "accommodation":
        return "숙소";
    }
  }

  function mappingIcon(type: string) {
    switch (type) {
      case "restaurant":
        return ICONS.RESTAURANT;
      case "tourist_attraction":
        return ICONS.TOUREIST_ATTRACTION;
      case "shopping":
        return ICONS.SHOPPING;
      case "experience":
        return ICONS.EXPERIENCE;
      case "accommodation":
        return ICONS.ACCOMMODATION;
      default:
        return ICONS.COST;
    }
  }

  return (
    <Wrapper>
      {courseData &&
        courseData.course.map((item, index) => (
          <React.Fragment key={index}>
            <CardWrapper onClick={() => handleCardClick(item)}>
              <IndexCircle>{index + 1}</IndexCircle>
              <ContentWrapper>
                <TitleWrapper>
                  <InfoWrapper>
                    <Category>{mappingCategory(item.type)}</Category>
                    <Title>{item.name}</Title>
                  </InfoWrapper>
                </TitleWrapper>
                <ImageWrapper>
                  {item.imageURL ? (
                    <Image src={item.imageURL} alt={item.name} />
                  ) : (
                    <NoImage>No image</NoImage>
                  )}
                </ImageWrapper>
              </ContentWrapper>
            </CardWrapper>
            {index < courseData.course.length - 1 && (
              <CarIconWrapper onClick={() => handleCarIconClick(index)}>
                <Icon name={ICONS.BUS} />
                <MoveTime>{courseData.moveMinutes[index]}분</MoveTime>
              </CarIconWrapper>
            )}
          </React.Fragment>
        ))}
    </Wrapper>
  );
};

export default Result;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  padding: 20px;
  height: 100%;
`;

const CardWrapper = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  background-color: ${(props) => props.theme.colors.background};
  box-shadow: ${(props) => props.theme.boxShadow};
  border-radius: 5px;
`;

const IndexCircle = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: ${(props) => props.theme.colors.primary};
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  margin: 0 10px;
`;

const ContentWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1;
  padding: 10px;
`;

const TitleWrapper = styled.div`
  display: flex;
  align-items: center;
`;

const InfoWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;

const Category = styled.div`
  font-size: 12px;
  color: ${(props) => props.theme.colors.semantic.success};
  margin-bottom: 3px;
`;

const Title = styled.div`
  font-size: 16px;
  font-weight: bold;
  color: ${(props) => props.theme.colors.primary};
`;

const ImageWrapper = styled.div`
  width: 80px;
  height: 80px;
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
`;

const NoImage = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #eee;
  color: #999;
  font-size: 12px;
`;

const CarIconWrapper = styled.div`
  display: flex;
  justify-content: start;
  width: 100px;
  background-color: ${(props) => props.theme.colors.neutral.lightGrey};
  border-radius: 0px 20px 20px 0px;
  padding: 0 10px;
  margin: 10px 0;
  cursor: pointer;
  &:hover {
    background-color: ${(props) => props.theme.colors.neutral.mediumGrey};
    transition: 0.2s ease-in;
    span {
      color: ${(props) => props.theme.colors.neutral.white};
      transition: 0.2s ease-in;
    }
  }
`;

const MoveTime = styled.span`
  font-size: 14px;
  color: ${(props) => props.theme.colors.neutral.mediumGrey};
  margin-top: 5px;
  margin-left: 8px;
`;
