import { atom } from "recoil";
import { Course, CourseData } from "@services/travelService";

export const travelSurveyResultState = atom<boolean>({
  key: "travelSurveyResultState",
  default: false,
});

export const selectedTravelState = atom<Course | null>({
  key: "selectedTravelState",
  default: null,
});

export const courseDataState = atom<CourseData | null>({
  key: "courseDataState",
  default: null,
});

interface Coord {
  lat: number;
  lng: number;
}
interface Route {
  start: Coord;
  end: Coord;
}
export const routeDataState = atom<Route | null>({
  key: "routeDataState",
  default: null,
});
